.ui-component-context .top-right-area {
  position: absolute;
  display: block;
  right: .5rem;
  top: .5rem;
  -webkit-transition: opacity 0.15s ease-out;
  transition: opacity 0.15s ease-out;
}
.ui-component-context .top-right-area .top-right-visible-manager {
  position: absolute;
  right: 0;
}
.ui-component-context .top-right-area .top-right-card {
    position: absolute;
    display: none;
    right: 0;
    z-index: 100;
}

.ui-component-context .top-right-area .top-right-visible-manager:hover + .top-right-card,
.ui-component-context .top-right-area .top-right-card:hover
{
    display: block;
}

.ui-component-context .edit-manager {
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.ui-component-context .layer-manager {
  position: relative;
  display: grid;
  grid-template-rows: 1fr;
  grid-gap: 1rem;
  gap: 1rem;
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 50vh;
}
.ui-component-context .layer-manager .layer {
  padding-right: 0.5rem;
}

.ui-component-context .layer-manager .layer .layer-main-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.ui-component-context .layer-manager .layer .layer-main-container .left {
  display: none;
}
.ui-component-context .layer-manager .layer.edit .layer-main-container .left {
  display: flex;
  flex-direction: column;
}
.ui-component-context .layer-manager .layer .layer-main-container .right {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 0.2rem;
}

.ui-component-context .layer-manager .layer .nested-main-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.ui-component-context .layer-manager .layer .nested-main-container .left {
  display: none;
}
.ui-component-context .layer-manager .layer.edit .nested-main-container .left {
  display: flex;
}
.ui-component-context .layer-manager .layer .nested-main-container .right {
  flex-grow: 1;
}

.ui-component-context .layer-manager .layer.edit {
  background-color: rgba(50, 50, 50, 0.15);
}

.ui-component-context .bottom-right-area {
  position: absolute;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ui-component-context .bottom-right-area .active {
  background-color: red;
}

.basemap-component{
    position: relative;
}

.basemap-component .basemap-visible-manager {
    margin: .5rem;
    /* opacity: .2; */
}
.basemap-component .basemap-content {
    position: absolute;
    margin: .3rem;
    display: none;
    left: auto;
    right: 0;
    bottom: 0;
    top: auto;
}
.basemap-component .basemap-visible-manager:hover + .basemap-content,
.basemap-component .basemap-visible-manager:active + .basemap-content,
.basemap-component .basemap-content:hover {
    display: block;
}

.map-settings-component {
    position: relative;
}
.map-settings-component .map-settings-visible-manager {

}
.map-settings-component .map-settings-content {
    display: none;
    position: absolute;
    right: 0;
    left: auto;
    bottom: 0;
    top: auto;
}
.map-settings-component .map-settings-visible-manager:hover + .map-settings-content,
.map-settings-component .map-settings-visible-manager:active + .map-settings-content,
.map-settings-component .map-settings-content:hover,
.map-settings-component .map-settings-content:active
{
    display: block;
}
